:root {
	--day-bar-height: 2.8rem;
	--navbar-height: 4.6rem;
	--home-button-bar-height: 0rem;

	/* Colors */

	/* Presets */
	--dark: #645a56;
	--red: #e66375;
	--orange: #ff7a51;
	--green: #26b182;
	--blue: #787ae4;
	--white: #faf2e7;
	/* transparent */
	--dark-1: #86756daa;
	--dark-2: #86756d66;
	--dark-3: #86756d33;
	--dark-4: #86756d22;
	--green-1: #98e6b0aa;
	--green-2: #98e6b066;
	--green-3: #98e6b022;
	--green-4: #98e6b011;
	--orange-1: #ff7a51aa;
	--orange-2: #ff7a5166;
	--orange-3: #ff7a5122;
	--orange-4: #ff7a5111;
	--blue-1: #787ae4cc;
	--blue-2: #787ae466;
	--blue-3: #787ae422;
	--blue-4: #787ae411;
	--white-1: #faf2e7aa;
	--white-2: #faf2e766;
	--white-3: #faf2e722;
	--white-4: #faf2e711;

	/* Week tables */
	--week-table-text: var(--dark);
	--week-table-background: var(--white);
	--week-table-highlight: var(--blue);

	/* Blocks */
	--block-background: var(--white);
	--block-text: var(--dark);
	--class-block-background: var(--white);
	--class-text: var(--dark);
	--class-text-highlight: var(--dark);
	--break-block-background: var(--blue-1);
	--break-text: var(--blue);
	--shadow-color: var(--dark-3);

	/* Buttons */
	--button-background: var(--white);
	--button-text: var(--dark);
	--orange-button-background: var(--orange);
	--orange-button-text: var(--white);
	--green-button-background: var(--green);
	--green-button-text: var(--white);
	--blue-button-background: var(--blue);
	--blue-button-text: var(--white);

	/* Timers */
	--timer-start-text: var(--dark);
	--timer-end-text: var(--dark);
	--timer-live: var(--green);
	--timer-live-pre-active: var(--red);

	--total-time-text: var(--dark);
	--total-time-background: transparent;

	/* Timeline */
	--time-line-active: var(--green);
	--time-line: var(--dark);

	/* Version stamp */
	--version-text: var(--dark);

	/* Background color */
	--body-background: #f3e1c8;

	user-select: none;
	vertical-align: baseline;
}

.dark-theme {
	/* Presets */
	--dark: #ddd6d4;
	--red: #e4264f;
	--green: #51bb8b;
	--white: #27282e;
	--blue: #8c8deb;
	/* transparent */
	--dark-1: #ddd6d4cc;
	--dark-2: #ddd6d466;
	--dark-3: #ddd6d422;
	--dark-4: #ddd6d411;
	--green-1: #98e6b0aa;
	--green-2: #98e6b066;
	--green-3: #98e6b022;
	--green-4: #98e6b011;
	--orange-1: #ff7a51aa;
	--orange-2: #ff7a5166;
	--orange-3: #ff7a5122;
	--orange-4: #ff7a5111;
	--blue-1: #8c8debaa;
	--blue-2: #8c8deb66;
	--blue-3: #8c8deb22;
	--blue-4: #8c8deb11;
	--white-1: #27282eaa;
	--white-2: #27282e66;
	--white-3: #27282e22;
	--white-4: #27282e11;

	/* Week tables */
	--week-table-text: var(--dark);
	--week-table-background: var(--white);
	--week-table-highlight: var(--blue);

	/* Blocks */
	--block-background: var(--white);
	--block-text: var(--dark);
	--class-block-background: var(--white);
	--class-text: var(--dark);
	--class-text-highlight: var(--dark);
	--break-block-background: var(--blue-1);
	--break-text: var(--blue);
	--shadow-color: transparent;

	/* Buttons */
	--button-background: var(--white);
	--button-text: var(--dark);
	--orange-button-background: var(--orange);
	--orange-button-text: var(--white);
	--green-button-background: var(--green);
	--green-button-text: var(--dark);
	--blue-button-background: var(--blue);
	--blue-button-text: var(--white);

	/* Timers */
	--timer-start-text: var(--dark);
	--timer-end-text: var(--dark);
	--timer-live: var(--green);
	--timer-live-pre-active: var(--red);

	--total-time-text: var(--dark);
	--total-time-background: transparent;

	/* Timeline */
	--time-line-active: var(--green);
	--time-line: var(--dark);

	/* Version stamp */
	--version-text: var(--orange);

	/* Background color */
	--body-background: #1d1d22;
	--body-background-1: #1d1d22aa;
	--body-background-2: #1d1d2266;
	--body-background-3: #1d1d2222;
	--body-background-4: #1d1d2211;
}

