@font-face {
	font-family: myFont;
	src: url("https://d-t-666.github.io/cxrili/fonts/bpg_glaho_sylfaen.ttf");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: numFont;
	src: url("https://d-t-666.github.io/cxrili/fonts/SourceCodePro-Medium.ttf");
	font-weight: normal;
	font-style: normal;
}

body {
	margin: 0;
	background-color: var(--body-background);
	transition: 0.4s;
}
